.sub-upper-style {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: flex-start;
  font-weight: bold;
  padding: 2px; /* Ajoute de l'espace autour des boutons à l'intérieur du conteneur */
}

.subUpper-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-items: flex-start;
  padding: 2px;
  list-style-type: none;
  margin: 0;
}

.subUpper-buttons button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 10px;
  margin:2px;
  cursor: pointer;
  font-weight: bold;
  border-radius: 5px;
  list-style-type: none;
}

.sub-upper-style button:hover {
    background-color: #0056b3;
}